body, input, button, select {
  font-family: 'Karla', sans-serif;
  font-size: 16px;
}

html,
body,
.container {
  height: 100%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.5em;
}

img {
  max-width: 100%;
}

input:focus {
  outline: 1px solid #E64C4C;
}

button {
  cursor: pointer;
  width: 100%;
  background: #E64C4C;
  text-transform: uppercase;
  color: #fff;
  padding: 10px;
  border: none;
}
button:focus {
  outline: 1px dashed #000;
}

select {
  border: 1px solid #dedede;
}
select:focus {
  outline: 1px solid #E64C4C;
}

input[type="text"] {
  width: 100%;
  border: 1px solid #dedede;
  height: 45px;
  padding: 5px;
  line-height: 35px;
}

.map {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.map img {
  max-width: 999999999px;
}
.map-overlay {
  position: absolute;
  z-index: 2;
  width: 300px;
  right: 20px;
  top: 80px;
  display: none;
  max-height: 90%;
  max-height: calc(100% - 160px);
  padding-top: 140px;
}
.map-overlay__month-select, .map-overlay__year-select {
  width: 50%;
  width: calc(50% - 5px);
  float: left;
  margin-top: 10px;
}
.map-overlay__year-select {
  margin-right: 10px;
}
.map-overlay__button {
  margin-top: 10px;
}
.map-overlay::before {
  content: "";
  display: block;
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  background: #fff;
  height: 150px;
}
.map-overlay::after {
  content: "";
  display: block;
  position: absolute;
  top: -20px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px;
  border-color: transparent transparent #fff transparent;
  right: 6px;
}
.map-overlay--show {
  display: flex;
}
.map-overlay__search {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 10px;
}
.map-overlay__police-data {
  background: #fff;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-in-out;
  width: 100%;
  overflow: auto;
}
.map-overlay__police-data--closing .map-overlay__police-data__section {
  transition-delay: 0s;
}
.map-overlay__police-data__total {
  padding: 10px 5px;
}
.map-overlay__police-data__section {
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
  transition-delay: 0.3s;
  position: relative;
  padding: 5px 0 5px 40px;
  cursor: pointer;
}
.map-overlay__police-data__section::before, .map-overlay__police-data__section::after {
  content: "";
  display: block;
  position: absolute;
  left: 10px;
  width: 20px;
  top: 5px;
  bottom: 5px;
}
.map-overlay__police-data__section::before {
  background: #efefef;
}
.map-overlay__police-data__section::after {
  background: #E64C4C;
  opacity: 0;
}
.map-overlay__police-data__section--active::before {
  opacity: 0;
}
.map-overlay__police-data__section--active::after {
  opacity: 1;
}
.map-overlay__police-data--show {
  transform: scaleY(1);
}
.map-overlay__police-data--show .map-overlay__police-data__section {
  opacity: 1;
}
.map-overlay__input-status {
  position: absolute;
  right: 10px;
  height: 45px;
  width: 45px;
  top: 0;
}
.map-overlay-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  background: none;
  border: none;
  width: auto;
}
.map-overlay-toggle:focus {
  outline: none;
}
.map-overlay-toggle svg {
  width: 20px;
}
.map-overlay-toggle:hover .map-overlay-toggle__icon--active, .map-overlay-toggle--open .map-overlay-toggle__icon--active {
  display: block;
}
.map-overlay-toggle:hover .map-overlay-toggle__icon--default, .map-overlay-toggle--open .map-overlay-toggle__icon--default {
  display: none;
}
.map-overlay-toggle__icon--active {
  display: none;
  fill: #E64C4C;
}


